import { Component, OnInit } from '@angular/core';
import axios from 'axios';
import { api } from '../server';
@Component({
  selector: 'app-nations',
  templateUrl: './nations.page.html',
  styleUrls: ['./nations.page.scss'],
})
export class NationsPage implements OnInit {

  constructor() { }


  nations = [];
  data = {
    nationalName: ''
  }
  ngOnInit() {
    this.getNations()
  }


  getNations() {
    axios.get(api + 'nationalities').then(res => {
      this.nations = res.data.reverse();
    })
  }

  addNation() {
    axios.post(api + 'addNationality', {
      nationalName: this.data.nationalName
    }).then(res => {
      this.data.nationalName = '';
      this.getNations()
    })
  }

}
